  display: none;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Public Sans", sans-serif;
  font-size: 1.6rem;
}
body.fijar-body {
  overflow: hidden;
}
body.body-scroll {
  padding-top: 7.76rem;
}

p {
  font-size: 2rem;
  color: #000;
  line-height: 1.5;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

h1, h2, h3 {
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 4rem;
}

h1 {
  font-size: 3.6rem;
}

h1 {
  font-size: 3rem;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

/* En SCSS, los mixins son bloques de código reutilizable que se pueden definir y utilizar en múltiples lugares de un archivo de estilo. Se definen con la directiva @mixin seguida del nombre del mixin y los argumentos que acepta, y se utilizan con la directiva @include seguida del nombre del mixin y los argumentos que se le pasan. Los mixins en SCSS son útiles para reducir la repetición de código y mejorar la legibilidad del archivo de estilo. */
header {
  background-color: rgb(1, 101, 0);
  margin-bottom: 4rem;
}

.navegacion {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.navegacion_logo {
  display: flex;
}
.navegacion_logo img {
  max-width: 5rem;
  background-color: #FFF;
  border-radius: 50%;
  border-color: #000;
  border-style: outset;
  border-color: #000;
  margin: 1rem;
}
.navegacion_logo p {
  color: #FFF;
  text-transform: uppercase;
}
.navegacion_logo p span {
  font-weight: 700;
}
@media (min-width: 768px) {
  .navegacion {
    flex-direction: row;
    justify-content: space-between;
  }
  .navegacion a {
    background: none;
    border-radius: 0;
  }
}

.navegacion_contenedor {
  display: flex;
  flex-direction: column;
  text-align: center;
  align-items: center;
  margin-top: 2rem;
}
.navegacion_contenedor a {
  color: #FFF;
  text-transform: uppercase;
  margin-bottom: 2rem;
  background-color: rgb(0, 50, 0);
  padding: 1rem;
  border-radius: 1rem;
  margin-top: 0.3rem;
}
.navegacion_contenedor a:hover {
  color: rgb(40, 11, 11);
}
@media (min-width: 768px) {
  .navegacion_contenedor {
    flex-direction: row;
    justify-content: space-between;
  }
  .navegacion_contenedor a {
    background: none;
    border-radius: 0;
  }
}